.mainInfo{
    background-color: rgb(43, 161, 161);
    padding: 2rem;
    border-radius: 4px;
    box-sizing: border-box;
}

.city{
    font-size: 4rem;
    text-align: center;
    font-weight: bolder;
}

.country{
    font-size: 20px;
    text-align: center;
}

.row{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.condition{
    font-size: 24px;
}

.current{
    font-size: 38px;
    font-weight: bold;
}
